@import "fonts.less";
@import "reset.less";

body.body {
    overflow: hidden;
}

.container {
    max-width: 1116px;
    margin: 0 auto;
    padding: 0 10px;
}

.header {
    font-size: 14px;
    .rm();
    color: #557571;
    &_container {
        padding: 0 55px;
        margin: 0 auto;
    }
    &_row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px 0 16px;
    }
    &_logo {
        
    }
    &_right {
        display: flex;
        align-items: center;
    }
    &_geo {
        display: flex;
        align-items: center;
        p {
            max-width: 155px;
            margin-left: 5px;
            margin-bottom: 0;
        }
    }
    &_mail {
        display: flex;
        align-items: center;
        padding-left: 35px;
        border-left: 1px solid #E9E9E9;
        margin-left: 35px;
        a {
            text-decoration-line: underline;
            color: #557571;
            margin-left: 5px;
            margin-bottom: 0;
            &:hover {
                text-decoration: none;
            }
        }
    }
    &_tel {
        display: flex;
        align-items: center;
        border-left: 1px solid #E9E9E9;
        padding-left: 30px;
        margin-left: 40px;
        .tel {
            font-size: 22px;
            line-height: 26px;
            letter-spacing: 0.065em;
            color: #F08771;
            margin-left: 5px;
            margin-bottom: 0;
        }
    }
    &_nav {
        background: #557571;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 70px;
    }
    &_ul {
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0;
        list-style: none;
    }
    &_li {
        margin: 0 12.5px;
        a {
            font-size: 15px;
            line-height: 20px;
            text-transform: uppercase;
            color: #FFFFFF;
            .os700();
            &:hover {
                text-decoration: none;
            }
            &::after {
                display:block;
                content: '';
                border-bottom: 1px solid #fff;   
                transform: scaleX(0);    
                transform-origin: 100% 50%;
                transition: transform 450ms ease-in-out;
            }
            &:hover::after {
                transform: scaleX(1);
                transform-origin: 0% 50%;
            }
        }
    }
}

.btn_mnu {
    display: none;
}

@media screen and (max-width:1025px) {
    .header {
        &_container {
            padding: 0 15px;
        }
        &_mail {
            margin-left: 15px;
            padding-left: 15px;
        }
        &_tel {
            margin-left: 15px;
            padding-left: 15px;
        }
    }
}

@media screen and (max-width:991px) {
    .btn_mnu {
        width: 50px;
        height: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: transparent;
        cursor: pointer;
        position: relative;
    }
    .btn_row {
        height: 2px;
        width: 60%;
        margin: 2px 0;
        background-color: #557571;
        position: relative;
        transition: all .5s;
    }
    .btn_mnu.active .btn_row:first-child {
        transform: rotate(-135deg);
        top: 6px;
    }
    .btn_mnu.active .btn_row:last-child {
        transform: rotate(-225deg);
        top: -6px;
    }
    .btn_mnu.active .btn_row:nth-child(2) {
        opacity: 0;
    }
    .btn_mnu.active .btn_row {
        background-color: #fff;
    }
    .btn_mnu.active {
        z-index: 101;
    }
    .header {
        &_nav {
            z-index: -10;
            opacity: 0;
            transition: .5s all;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: fixed;
            top: 0; left: 0;
            &.active {
                z-index: 100;
                opacity: 1;
            }
        }
        &_geo,&_mail {
            display: none;
        }
        &_ul {
            flex-direction: column;
        }
        &_li {
            margin: 7px 0;
        }
        &_tel {
            border-left: inherit;
            padding-left: 0px;
            margin-right: 15px;
        }
    }
}

